body{
    background-color: black;
    background-repeat: no-repeat;
    background-size: cover;
}

.board {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(8, 80px);
    width: 80%;
    margin: 50px auto;
}

.main-div {
    width: 100%;
    margin: 0px auto;
}

.chip {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Poppins;
    font-weight: bold;
    margin: 10px;
    background: #c6c0a8;
    cursor: pointer;
}

.chip:hover {
    opacity: 0.8;
}

.timer-div {
    font-family: Poppins;
    display: flex;
    justify-content: center;
    align-items: center;

}

.timer-div button {
    padding: 5px 25px;
    font-size: 24px;
    cursor: pointer;
    border-radius: 2px;
    border: none;

    font-family: Poppins;
    box-shadow: 1px 1px 10px grey;
}

.timer {

    display: flex;
    justify-content: center;
    align-items: center;

    position: fixed;
    top: 20px;
    right: 10px;

    height: 100px;
    width: 100px;
    color: white;
    border: 4px solid white;
    border-radius: 50%;
    font-size: 28px;
    font-family: "Russo One";
}

.numbers p {
    text-align: center;
    margin: 25px auto;
    font-size: 18px;
    font-family: Poppins;
    color: white;
}

.nums {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.nums .selected {
    height: 100px;
    width: 100px;
    font-size: 20px;
    background: rgb(219, 184, 118);
    font-family: "Russo One";
}


.treasure {
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box {
    text-align: center;
    padding: 10px;
    height: 70px;
    width: 100px;
    font-family: "Russo One";
    font-size: 18px;
    background: #bae4ba;
    cursor: pointer;
}

.treasure .title {

    z-index: 10;
}

@keyframes rotate {
    0% {
        transform: rotateX(0deg);
    }

    100% {
        transform: rotateX(90deg);
    }
}

.treasure .value {
    position: absolute;
    margin-left: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: -1;
}

.results {
    text-align: center;
    font-family: Poppins;
    font-size: 20px;
    margin-top: 25px;
    color: white;
}

.try {
    padding: 5px 10px;
    margin-left: 15px;
    font-family: Poppins;
    font-size: 20px;
    border-radius: 2px;
    border: none;
    box-shadow: 1px 1px 10px grey;
    outline: none;
    color: black;
    text-decoration: none;
}

.instructions {
    margin: 40px;
    color: white;
}

.instructions p {
    font-family: Poppins;
    font-size: 18px;
}

.instructions span {
    margin-right: 5px;
}

@media screen and (max-width : 600px) {
    body {
        background-image: none;
        background: rgb(27, 26, 26);
    }

    .instructions {
        margin: 30px;
    }

    .instructions p {
        font-family: Poppins;
        font-size: 14px;

    }

    .timer-div button {
        padding: 5px 15px;
        font-size: 16px;
    }

    .timer {
        top: 40px;
        right: 10px;

        height: 50px;
        width: 50px;
        border: 3px solid white;
        font-size: 18px;
    }

    .chip {
        font-size: 12px;
        height: 40px;
        width: 30px;
    }

    .numbers p {
        font-size: 14px;
    }

    .nums .selected {
        height: 40px;
        width: 40px;
        font-size: 14px;
    }

    .box {
        height: 50px;
        width: 60px;
        font-size: 14px;
    }

    .results {
        font-size: 16px;
        margin-top: 25px;
    }

    .try {
        padding: 3px 7px;
        margin-left: 15px;
        font-size: 16px;
    }
}